import React, {Component} from 'react';
import {connect} from "react-redux";
import {signIn} from '../../store/actions/authActions'
import {Link, Redirect} from 'react-router-dom'

class SignIn extends Component{
    state = {
        email: '',
        password: ''
    };

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    };

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.signIn(this.state)
    };

    render() {
        const {authError, auth} = this.props;
        if(auth.uid) return <Redirect to='/'/>;

        return (
            <div className='container'>
                <Link className="btn-floating btn-large waves-effect waves-light red right" to='/signup'>
                    <i className="material-icons">add</i>
                </Link>
                <form onSubmit={this.handleSubmit} className='white'>
                    <h5 className="grey-text text-darken-3">Sign In</h5>
                    <div className="input-field">
                        <label htmlFor="email">Email</label>
                        <input type="email" id='email' onChange={this.handleChange}/>
                    </div>
                    <div className="input-field">
                        <label htmlFor="password">Password</label>
                        <input type="password" id='password' onChange={this.handleChange}/>
                    </div>
                    <div className="input-field">
                        <button className="btn deep-purple darken-3 z-depth-0">Login</button>
                    </div>

                    <div className="red-text center">
                        {authError ? <p>{authError}</p>: null}
                    </div>
                    <div className="center">
                        <img className='logoAuth' alt='logo' src='./img/westcasino-logo-143x60-01.svg'/>
                    </div>
                </form>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        authError: state.auth.authError,
        auth: state.firebase.auth
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        signIn: (creds) => dispatch(signIn(creds))
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(SignIn);