import React, {Component} from 'react';
import {connect} from "react-redux";
import {Redirect} from 'react-router-dom'
import {signUp} from "../../store/actions/authActions";
import {Link} from "react-router-dom";

class SignUp extends Component{
    state = {
        email: '',
        password: '',
        firstName: '',
        lastName: '',
    };

    handleChange = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        })
    };

    handleSubmit = (e) => {
        e.preventDefault();
        this.props.signUp(this.state)
    };

    render() {
        const { auth, authError} = this.props;
        if(auth.uid) return <Redirect to='/'/>;

        return (
            <div className='container'>
                <form onSubmit={this.handleSubmit} className='white'>
                    <h5 className="grey-text text-darken-3">Sign Up</h5>
                    <div className="input-field">
                        <label htmlFor="email">Email</label>
                        <input type="email" id='email' onChange={this.handleChange}/>
                    </div>
                    <div className="input-field">
                        <label htmlFor="password">Password</label>
                        <input type="password" id='password' onChange={this.handleChange}/>
                    </div>
                    <div className="input-field">
                        <label htmlFor="firstName">First Name</label>
                        <input type="text" id='firstName' onChange={this.handleChange}/>
                    </div>
                    <div className="input-field">
                        <label htmlFor="lastName">Last Name</label>
                        <input type="text" id='lastName' onChange={this.handleChange}/>
                    </div>
                    <div className="input-field">
                        <button className="btn deep-purple darken-3 z-depth-0">Sign Up</button>
                        <Link className="right" to='/signin'>
                            <p>Sign In <i className="material-icons">arrow_forward</i></p>
                        </Link>
                        <div className="red-text center">
                            {authError ? <p>{authError}</p>: null }
                        </div>
                        <div className="center">
                            <img className='logoAuth' alt='logo' src='./img/westcasino-logo-143x60-01.svg'/>
                        </div>
                    </div>
                </form>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        auth: state.firebase.auth,
        authError: state.auth.authError
    }
};
const mapDispatchToProps = (dispatch) => {
    return {
        signUp: (newUser) => dispatch(signUp(newUser))
    }
};


export default connect(mapStateToProps,mapDispatchToProps)(SignUp);